import React, { useEffect, useState, useRef } from "react"
import "../styles/App.scss"
import { gantt } from "dhtmlx-gantt"
import Gantt from "./Gantt.js"
import Sidebar from "./Sidebar.js"
import withPersistence from "../hocs/withPersistence.js"
import withGitHubData from "../hocs/withGitHubData.js"

import { Sidebar as SidebarDHX, TreeCollection } from "dhx-suite"
import "dhx-suite/codebase/suite.min.css"

const MilestonesGantt = ({ sdk, github, persistence }) => {
  const ganttContainer = useRef()
  const [selectedOrganization, setSelectedOrganization] = useState()
  const [organizations, setOrganizations] = useState([])

  gantt.attachEvent("onTaskClosed", id => {
    persistence.set(id, "closed")
  })

  gantt.attachEvent("onTaskOpened", id => {
    persistence.remove(id)
  })

  gantt.attachEvent("onParse", () => {
    gantt.eachTask(task => {
      if (persistence.get(task.id) === "closed") {
        task.$open = false
      }
    })
  })

  return (
    <div className=".milestones">
      <div className="gantt-container">
        <Sidebar />
        {
          github.data && <Gantt tasks={github.data} zoom="Months" />
        }
      </div>
    </div>
  )
}

export default withPersistence(withGitHubData(MilestonesGantt))
